.out {
	background: linear-gradient(201deg, #376d5f, #30d1a7, #b3f6e5, #688d84, #2965c2);
	background-size: 1000% 1000%;
	animation: AnimationName 46s ease infinite; }

@keyframes AnimationName {
    0% {
    	background-position: 84% 0%; }
    50% {
    	background-position: 17% 100%; }
    100% {
    	background-position: 84% 0%; } }

.content {
	@include flexbox;
	padding: 250px 100px;
	@include justify-content(center);
	@include align-items(baseline);
	@include flex-wrap(wrap);
	max-width: 1200px;
	min-height: 1200px;
	margin: 0 auto; }

.svg-box {
	@include flex(0 0 100%);
	text-align: center;
	margin-bottom: 100px;
	svg {
		width: 100%;
		height: 500px;
		padding: 65px 25%; } }
.button {
	&_trigger {
		display: inline-block;
		min-width: 88px;
		min-height: 40px;
		margin: 0 auto;
		padding: 0.66em 1.22em;
		font-weight: 300;
		line-height: 1.4;
		@include borad(2px,all);
		font-family: 'Montserrat', sans-serif;
		font-size: 1rem;
		background-color: #333;
		color: #fff; } }

